import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
import { map, switchMap, take } from 'rxjs/operators';
import { ReportData } from 'src/app/data/report-data';
import { ReportResponse } from 'src/app/data/report-response';
import { SheetData } from 'src/app/data/sheet-data';
import { SheetDataResponse } from 'src/app/data/sheet-data-response';
import { environment } from 'src/environments/environment';
import { Observable } from 'rxjs';
import { Item } from 'src/app/test/item';
import { Body } from 'src/app/test/body';
import { Header } from 'src/app/test/header';
import { Report } from 'src/app/test/report';

import { toXML } from "jstoxml";
import * as JSZip from 'jszip';
import { saveAs } from "file-saver";


@Injectable({
  providedIn: 'root'
})
export class ReportService {

  tableData: Array<ReportData> = null;

  private readonly $reportData = new BehaviorSubject<Array<ReportData>>(null);
  private $reportSelectedDate = new BehaviorSubject<string>(null);
  private readonly $fileId = new BehaviorSubject<string>(null);

 sheetData: Array<SheetData> = null;
 _filedId = '';
 private reportGroup: string
  reportsHolder: ReportData[];

  constructor(
    private readonly http: HttpClient
  ) {}

  getSheetData(sheetName: string) {

    return this.selectedDate.pipe(take(1), switchMap(date => {
      return this.http.get<SheetDataResponse>(`${environment.baseApi}/${sheetName.toLowerCase()}/${date}`);
    }), map((response) => {
      let sheetData: Array<SheetData> = null;
      const columns = response.columnNames;
      // const editable = response.editable;
      for (const key in response) {
        if (Object.prototype.hasOwnProperty.call(response, key)) {
          if (key !== 'responseCode' && key !== 'responseMessage' && key.toLowerCase().includes('sheet')) {
            sheetData = response[key] as  Array<SheetData>;
          }
        }
      }
      // editable
      return {sheetData, columns};
    }));
  }

  setReportGroup(group: string) {
    this.reportGroup = group;
    return this.http.get(`${environment.baseApi}/tableList/${this.reportGroup}`)
  }

  getReportGroup() {
    return this.reportGroup
  }

  getReports(reportType: string) {
    this.$reportData.next(null)
    return this.selectedDate.pipe(take(1), switchMap(date => {
      return this.http.get<ReportResponse>(`${environment.baseApi}/tableList/` + this.reportGroup)
    }), map((response) => {
      console.log(this.reportGroup);
      this.$reportData.next(response.data);
      this.tableData = response.data;
      console.log("Inside Service: ", response.data)
      return response.data;
    }));
  }

  // new get reports

  getReportByGroup(reportType: string) {
    this.$reportData.next(null)
    return this.selectedDate.pipe(take(1), switchMap(date => {
      return this.http.get<ReportResponse>(`${environment.baseApi}/tableList/${reportType}/${this.reportGroup}`)
    }), map((response) => {
      this.$reportData.next(response.data);
      this.tableData = response.data;
      console.log(response.data)
      return response.data;
    }));
  }
  updateSheet(data: SheetData, sheetType: string) {
    return this.selectedDate.pipe(take(1), switchMap(date => {
      return this.http.put(`${environment.baseApi}/${sheetType.toLowerCase()}/${date}/${data.id}`, data);

    }), map((response) => {
      for (const key in response) {
        if (Object.prototype.hasOwnProperty.call(response, key)) {
          if (key !== 'responseCode' && key !== 'responseMessage' && key.toLowerCase().includes('data')) {
            return response[key] as  SheetData;
          }
        }
      }
    }));
  }

  updateSpecialSheet( id: number, body: any, sheetType: string, length: number, code: string) {
    console.log(id, body, sheetType, code);
    return this.selectedDate.pipe(take(1), switchMap(date => {
      return this.http.put(`${environment.baseApi}/${sheetType.toLowerCase()}/${date}/${id}`, {
        ...body,
        code
      });

    }), map((response) => {
      for (const key in response) {
        if (Object.prototype.hasOwnProperty.call(response, key)) {
          if (key !== 'responseCode' && key !== 'responseMessage' && key.toLowerCase().includes('data')) {
            return response[key] as  SheetData;
          }
        }
      }
    }));
  }

  getTableData() : Array<ReportData> {
    return this.tableData;
  }

  createData(data: SheetData, sheetType: string){
    return this.selectedDate.pipe(take(1), switchMap(date => {
      return this.http.post(`${environment.baseApi}/${sheetType.toLowerCase()}/${date}`, data)
    }), map((response) => {
      for (const key in response){
            if (key !== 'responseCode' && key !== 'responseMessage' && key.toLowerCase().includes('data')){
              return response[key] as SheetData;
            }
      }


    }))
  }

  deleteData(data:SheetData, sheetType:string){
    return this.selectedDate.pipe(take(1), switchMap(date => {
      return this.http.delete(`${environment.baseApi}/${sheetType.toLowerCase()}/${date}/${data.id}`)
    }), map((responseMessage)=>{
      return responseMessage;
    }))

  }

  setReportDate(date:string) {
    this.$reportSelectedDate.next(date);
  }

  saveReportDate() {
    return this.selectedDate.pipe(take(1), switchMap((date) => {
      return this.http.get<{responseMessage: string, responseCode: number}>(`${environment.baseApi}/report/date/${date}`).pipe(map(res => {
        // this.$fileId.next(res.fileId);
        // this._filedId = res.fileId;
        return res;

      }));
    }));
  }

  clearReportDate() {
    this.$reportSelectedDate.next('');
  }

  generateReport() {
    return this.selectedDate.pipe(take(1), switchMap((date) => {
      if (date) {
        return this.http.get(`${environment.baseApi}/generate/${date}/${this._filedId}`);
      }
      throw new Error('Date not selected');
    }));
  }

  get reportData() {
    return this.$reportData.asObservable();
  }

  get selectedDate() {
    return this.$reportSelectedDate.asObservable();
  }

  get fileId() {
    return this.$fileId.asObservable();
  }

  testGenerateReport() {
    let zip = new JSZip();
    const options = {
      header: true,
      indent: ' '
    }
    for (let report of this.reports) {
      let rep = toXML(report, options)
      let blob = new Blob([rep], {type: "application/xml;charset=utf-8"})
      zip.file(report.header.returnCode+".xml", blob);

    }
    let date = new Date().toLocaleDateString()
    zip.generateAsync({type:"blob"})
    .then(function(content) {
      // jstoxml, jszip, file-saver
        saveAs(content, "Reports Generated on "+date+".zip");
    });
  }


  item1:Item = new Item('2001', 'Test Code 1', 45000)
  item2:Item = new Item('2002', 'Test Code 2', 75000)
  item3:Item = new Item('2003', 'Test Code 3', 56000)
  item4:Item = new Item('2004', 'Test Code 4', 50000)
  item5:Item = new Item('2005', 'Test Code 5', 15000)
  items:Item[] = [
    this.item1,
    this.item2,
    this.item3,
    this.item4,
    this.item5,
  ]

  header1 = new Header(
    '00405',
    'NIGERIAN EXPORT IMPORT BANK (NEXIM)',
    'MDFIR300',
    'SCHEDULE OF QUOTED EQUITY INVESTMENTS',
    '01/04/2021',
    '30/06/2021',
    'ORIG',
    '',
    'en_US'
  )

  body1:Body = new Body('MDFIR300',
  'Monthly Statement of Assets and Liabilities',
  this.items)

  header2 = new Header(
    '00405',
    'NIGERIAN EXPORT IMPORT BANK (NEXIM)',
    'MDFIR1000',
    'SCHEDULE OF QUOTED EQUITY INVESTMENTS',
    '01/04/2021',
    '30/06/2021',
    'ORIG',
    '',
    'en_US'
  )

  body2 = new Body('MDFIR1000',
  'Monthly Statement of Assets and Liabilities',
  this.items)

  header3 = new Header(
    '00405',
    'NIGERIAN EXPORT IMPORT BANK (NEXIM)',
    'MDFIR1200',
    'SCHEDULE OF QUOTED EQUITY INVESTMENTS',
    '01/04/2021',
    '30/06/2021',
    'ORIG',
    '',
    'en_US'
  )

  body3 = new Body('MDFIR1200',
  'Monthly Statement of Assets and Liabilities',
  this.items)

  reports:Report[] = [
    new Report(this.header1, this.body1),
    new Report(this.header2, this.body2),
    new Report(this.header3, this.body3),
    new Report(this.header1, this.body1),
    new Report(this.header2, this.body2)
  ]


  testGenerateReportsSpecific(reports: Report[]) {
    let zip = new JSZip();
    const options = {
      header: true,
      indent: ' '
    }

    if (reports.length <= 0) {
      reports = this.reports
    }

    for (let report of reports) {
      let rep = toXML(report, options)
      let blob = new Blob([rep], {type: "application/xml;charset=utf-8"})
      zip.file(report.header.returnCode+".xml", blob);
    }

    let date = new Date().toLocaleDateString()
    zip.generateAsync({type:"blob"})
    .then(function(content) {
      // jstoxml, jszip, file-saver
        saveAs(content, "Reports Generated on "+date+".zip");
    });
  }

  testGenerateSingleReport(report: Report) {
    let zip = new JSZip();
    const options = {
      header: true,
      indent: ' '
    }
    let rep = toXML(report, options)
    let blob = new Blob([rep], {type: "application/xml;charset=utf-8"})
    zip.file(report.header.returnCode+".xml", blob);

    let date = new Date().toLocaleDateString()
    zip.generateAsync({type:"blob"})
    .then(function(content) {
      // jstoxml, jszip, file-saver
        saveAs(content, "Reports Generated on "+date+".zip");
    });
  }


  testGenerateReportsCheck(reports) {
    this.reportData.subscribe((_reportData) => {
      this.reportsHolder = _reportData;
    });
    let zip = new JSZip();
    const options = {
      header: true,
      indent: ' '
    }
    if (reports.length <= 0) {
      reports =  this.reportsHolder
    }

    for (let report of reports) {
      let rep = toXML(report, options)
      let blob = new Blob([rep], {type: "application/xml;charset=utf-8"})
      zip.file(report.sheetNumber+".xml", blob);
    }

    let date = new Date().toLocaleDateString()
    zip.generateAsync({type:"blob"})
    .then(function(content) {
      // jstoxml, jszip, file-saver
        saveAs(content, "Reports Generated on "+date+".zip");
    });
  }

}
