import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AppComponent } from './app.component';
import { AuthenticatedGuard } from './common/authenticated.guard';
import { UnauthenticatedGuard } from './common/unauthenticated.guard';

const routes: Routes = [

  {path: '', redirectTo: '/login', pathMatch: 'full'},
  {path: 'login', loadChildren: () => import('./pages/login/login.module').then( m => m.LoginModule), canActivate: [UnauthenticatedGuard]},
  // tslint:disable-next-line: max-line-length
  {path: 'dashboard', canLoad: [AuthenticatedGuard], loadChildren: () => import('./pages/dashboard/dashboard.module').then( m => m.DashboardModule)},
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
