import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
import { map, switchMap, take } from 'rxjs/operators';
import { ReportData } from 'src/app/data/report-data';
import { ReportResponse } from 'src/app/data/report-response';
import { SheetData } from 'src/app/data/sheet-data';
import { SheetDataResponse } from 'src/app/data/sheet-data-response';
import { environment } from 'src/environments/environment';
import { Observable } from 'rxjs';


@Injectable({
  providedIn: 'root'
})
export class GeneralService {
  private readonly blockUI$ = new BehaviorSubject<boolean>(false);
  constructor(
    private readonly http: HttpClient
  ) {}

  updateBlockedUI(block: boolean): void {
    this.blockUI$.next(block);
  }
  get blockUI(): Observable<boolean> {
    return this.blockUI$.asObservable();
  }
}
