import { HttpBackend, HttpClient } from '@angular/common/http';
import { Injectable, NgZone } from '@angular/core';
import { BehaviorSubject, from, of, throwError } from 'rxjs';
import { environment } from 'src/environments/environment';
import {catchError, map, switchMap, take, tap} from 'rxjs/operators';
import { LoginResponse } from '../data/login-response';
import { LoginRequest } from '../data/login-request';
import { User } from '../data/user';
import { UserLoginData } from '../data/user-login-data';
import {Idle, DEFAULT_INTERRUPTSOURCES} from '@ng-idle/core';
import { ReportService } from '../pages/generate-report/report.service';

@Injectable({
  providedIn: 'root'
})
export class AuthService {

  private readonly $user = new BehaviorSubject<User>(null);
  private readonly $isAuthenticated = new BehaviorSubject<boolean>(false);
  private readonly $token = new BehaviorSubject<string>(null);
  private readonly USER_AUTH_KEY = '@USER_AUTH_KEY';

  private httpWithoutInterceptor: HttpClient;

  constructor(
    private readonly httpClient: HttpClient,
    private readonly httpBackend: HttpBackend,
    private idle: Idle,
    private readonly ngZone: NgZone,
    private readonly reportService: ReportService
  ) {
    this.httpWithoutInterceptor = new HttpClient(httpBackend);
   }

  login(loginData: LoginRequest) {
    return this.httpWithoutInterceptor.post<LoginResponse>(`${environment.baseUrl}/authenticate`, loginData).pipe(map((response) => {
      this.$user.next(response.user);
      this.$isAuthenticated.next(true);
      this.$token.next(response.token);
      const userAuthData: UserLoginData = {
        user: response.user,
        token: response.token
      }
      const userAuthStringData = JSON.stringify(userAuthData);
      localStorage.setItem(this.USER_AUTH_KEY, userAuthStringData);
      this.autoLogout();
      return true;
    }, async err => {
      this.$user.next(null);
      this.$isAuthenticated.next(false);
      this.$token.next(null);
      return false;
    }));
  }

  autoLogin() {
    const userData = JSON.parse(localStorage.getItem(this.USER_AUTH_KEY)) as UserLoginData;
    if (userData?.token && userData?.user) {
      this.$user.next(userData.user);
      this.$token.next(userData.token);
      this.$isAuthenticated.next(true);
      this.autoLogout();
    } else {
      this.$user.next(null);
      this.$token.next(null);
      this.$isAuthenticated.next(false);
    }
  }


  logout() {
    this.reportService.clearReportDate();
    localStorage.removeItem(this.USER_AUTH_KEY)
    this.$user.next(null);
    this.$token.next(null);
    this.$isAuthenticated.next(false);
  }

  autoLogout() {
    this.idle.onTimeout.subscribe(() => {
      this.ngZone.run(() => {
        this.logout();
      });
    });
    this.idle.setTimeout(60)
    this.idle.setInterrupts(DEFAULT_INTERRUPTSOURCES);
    this.idle.watch();
  }

  get user() {
    return this.$user.asObservable();
  }

  get isAuthenticated() {
    return this.$isAuthenticated.asObservable();
  }

  get token() {
    return this.$token.asObservable();
  }
}
